html {
    font-size: 10px;
    box-sizing: border-box;
    scrollbar-color: #252c32 #0a0e13;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent
}

body {
    overflow-y: scroll;
    background-color: #0f1519;
    color: #fff;
    line-height: 1.3125em;
    font-family: 'Colfax', Helvetica, sans-serif;
    font-size: 1.6rem;
    word-break: keep-all;
}

body.no-scroll {
    overflow: hidden
}

@media (max-width:1075px) {
    body.no-scroll-small-medium {
        overflow: hidden
    }
}

@media (max-width:720px) {
    body.no-scroll-small {
        overflow: hidden
    }
}

@media (pointer:coarse) {
    @supports (-webkit-overflow-scrolling: touch) {
        body {
            cursor:pointer
        }
    }
}

.browserupgrade {
    padding: 10px;
    color: #eee;
    text-align: center
}

*, *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: inherit
}

:focus {
    outline: 0
}

a {
    color: #fff;
    text-decoration: none
}

a:active, a:hover {
    outline: 0
}

img:not([src]) {
    visibility: hidden
}

@media (pointer:fine) {
    ::-webkit-scrollbar {
        height: 14px;
        width: 14px;
        background-color: #0a0e13;
        margin: 0 10px
    }
    ::-webkit-scrollbar-thumb {
        background: #252c32;
        -webkit-border-radius: 1ex;
        border: 3px solid #0a0e13
    }
    ::-webkit-scrollbar-corner {
        background: #000
    }
}

.sidebar {
    width: calc(((100% - 442px) / 12) * 3 + 102px);
    min-height: 100vh;
    min-width: 360px;
    background: #0a0e13
}

h1, h2, h3, h4, h5, h6 {
    font-weight: normal
}

h1 {
    font-size: 2rem
}

h2 {
    font-size: 1.8rem
}

h3 {
    font-size: 1.6rem
}

h4 {
    font-size: 1.4rem;
    letter-spacing: .08em
}

h5 {
    font-size: 1.2rem;
    letter-spacing: .05em
}

h6 {
    font-size: 1rem;
    letter-spacing: 0
}

@media (min-width:550px) {
    h1 {
        font-size: 2.4rem
    }
    h2 {
        font-size: 1.8rem
    }
    h3 {
        font-size: 1.6rem
    }
    h4 {
        font-size: 1.4rem
    }
    h5 {
        font-size: 1.2rem
    }
    h6 {
        font-size: 1rem
    }
}

img {
    border: 0
}

.blur-enabled {
    filter: blur(4px)
}



