@media all {
    .VodsFilter {
        animation: fadein .8s
    }
    .VodsFilter .title {
        color: #8fa3b0;
        font-size: 1.4rem;
        letter-spacing: .1rem;
        font-weight: bold;
        padding: 37px 0 16px 30px;
        border-bottom: 1px solid #252c32
    }
    .VodsFilter .leagues {
        overflow-x: hidden;
        overflow-y: auto;
        padding-bottom: 80px;
    }
    .VodsFilter .leagues .league {
        position: relative;
        cursor: pointer;
        list-style: none;
        border-bottom: 1px solid #252c32;
        color: #8fa3b0;
        transition: background .25s ease-out;
    }
    .VodsFilter .leagues .league:after {
        display: block;
        position: absolute;
        top: 0;
        width: 4px;
        height: 100%;
        content: '';
        z-index: 100;
        background: transparent;
        transition: background .25s ease-out
    }
    .VodsFilter .leagues .league.selected:after, .VodsFilter .leagues .league.selected:hover:after {
        background: #00c8c8
    }
    .VodsFilter .leagues .league.selected .info .image {
        opacity: 1
    }
    .VodsFilter .leagues .league.selected .info .label .name {
        color: #fff
    }
    .VodsFilter .leagues .league .info {
        display: flex;
        align-items: center;
    }
    .VodsFilter .leagues .league .info .image {
        display: inline-block;
        float: left;
        opacity: .3;
        transition: opacity .1s ease-out
    }
    .VodsFilter .leagues .league .info .label {
        padding-top: 2px;
    }
    .VodsFilter .leagues .league .info .label .name {
        font-size: 1.8rem;
        font-weight: normal;
        color: #8fa3b0
    }
    .VodsFilter .leagues .league .info .label .region {
        text-transform: uppercase;
        color: #8fa3b0;
        font-size: 1.4rem;
        letter-spacing: .1rem;
        font-weight: bold
    }
}

@media (min-width:1076px) {
    .VodsFilter {
        position: fixed;
        height: calc(100vh - 80px);
        width: calc(((100% - 442px) / 12) * 3 + 102px);
        border-right: 1px solid #252c32
    }
    .VodsFilter .title {
        height: 75px
    }
    .VodsFilter .leagues {
        height: calc(100% - 75px);
    }
    .VodsFilter .leagues .league:after {
        right: 0
    }
    .VodsFilter .leagues .league:hover {
        background: #000;
        transition: background .1s ease-out;
    }
    .VodsFilter .leagues .league:hover:after {
        background: #252c32;
        transition: background .1s ease-out
    }
    .VodsFilter .leagues .league:hover .image {
        opacity: 1;
        transition: opacity .1s ease-out
    }
    .VodsFilter .leagues .league:hover .label .name {
        color: #fff;
        transition: color .1s ease-out
    }
    .VodsFilter .leagues .league .info {
        padding: 12px 0;
    }
    .VodsFilter .leagues .league .info .image {
        width: 60px;
        height: 60px;
        margin: 0 12px 0 24px
    }
}

@media (min-width:721px) and (max-width:1075px) {
    .VodsFilter {
        height: calc(100vh - 80px);
        width: 100%;
        border-right: 1px solid #252c32
    }
    .VodsFilter .title {
        height: 75px
    }
    .VodsFilter .leagues {
        height: calc(100% - 75px);
    }
    .VodsFilter .leagues .league:after {
        right: 0
    }
    .VodsFilter .leagues .league .info {
        padding: 12px 0;
