@media (min-width:1076px) {
    .Watch {
        flex-direction: row;
        max-height: 475px;
        border: 0.2px solid rgba(204, 204, 204, 0.205);
    }
    .Watch .overview-pane {
        width: calc(((100% - 442px) / 12) * 4 + 136px);
        min-width: 400px;
        max-width: 480px;
        max-height: 475px;
    }
    .Watch .center-pane {
        width: 100%;
    }
    .Watch .center-pane .lower {
        flex-direction: row
    }
    .Watch .chat-pane {
        min-width: 400px
    }
    .Watch .nav {
        height: 60px;
        border-bottom: 1px solid #252c32
    }
}

@media (min-width:721px) and (max-width:1075px) {
    .Watch {
